* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  position: relative;
  background-image: url(/covid19-stats/static/media/image1.2576fc59.jpg);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
  color: rgb(238, 238, 238);
  z-index: -2;
}

body::after {
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  background: #1b1b1bdc;
  z-index: -1;
}

a {
  text-decoration: none;
  color: bisque;
}
a:hover {
  border-bottom: 1px solid bisque;
}

